.explore {
    width: 100%;
    height: 100vh;
    background: linear-gradient(90deg, rgba(2, 0, 36, 0.9) 41%, rgba(201, 201, 210, 0.5) 82%, rgba(222, 228, 230, 0.5) 100%), url("../Images/leaders.jfif");
    opacity: 1;
}

.exploredata {
    text-align: left;
    padding: 8% 2%
}

.exploretitle {
    color: white;
}

.exploretitle span {
    color: #FD6C22;
}

.exploredata p {
    color: rgb(192, 188, 188);
}

.profilecolor {
    color: white;
    background-color: #FD6C22;
}

.topleader {
    padding: 8px 14px;
    margin: 2%;
    width: 100%;
}

.topleader:hover {
    color: white;
    background-color: #FD6C22;
}

.knowmore {
    padding: 8px 14px;
    cursor: pointer;
    color: white;
    font-size: 20px;
}

.create {
    background-color: #FCFCFE;
    height: 50h;
    box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
    border-radius: 14px;
    margin: 45% 6%;
    padding: 4% 6%;
}

.create label {
    float: left;
    margin-bottom: 2%;
}

.create h4,
p {
    text-align: left;
}

.create p {
    color: rgb(161, 152, 152);
}

.create input {
    margin-bottom: 2%;
}

.terms {
    text-align: left;
    color: rgb(161, 152, 152);

}

.terms label {
    padding-left: 2%;
}

.btnsignup {

    width: 100%;
    margin: 3% 0%;
}

.btnsignup:hover {
    color: white;
    background-color: #FD6C22;
}

.haveaccount {
    text-decoration: underline;
    color: rgb(161, 152, 152);
    cursor: pointer;
}
.terms .form-check-input:checked {
    background-color: #FD6C22;
    border-color:#FD6C22;
}
.terms .form-check-input:focus {
    border-color: #FD6C22;
    outline: 0;
    box-shadow: none;
}
.terms .form-check-input:checked[type=checkbox] {
    color: #FD6C22;
}
.profilesearch {
    background-color: #FCFCFE;
    border: 1px solid rgb(233, 230, 230);
    border-radius: 12px;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
    padding: 4%;
    margin: 2%
}

.profilecard {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 20px;
}

.profilelistcard {
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    border-radius: 10px;
    background-color: #fff;
    padding: 4%;
    margin: 2%;
}

.profiletitle {
    color: #FD6C22;
    background-color: #FFF0DD;
    padding: 6px;
    border-radius: 9px;
}

.profileimage {
    width: 60%;
    border-radius: 9px;
}

.loc {
    margin-right: 10px;
}

.viewprofile {
    float: right;
}

.socialicons {
    margin: 12%;
}

.redcolor {
    color: red;
}

.datasearch {
    border: none !important;
    box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
    background: url("https://static.thenounproject.com/png/101791-200.png") no-repeat left #FCFCFE;
    background-size: 20px;
    background-position: 12px;
    margin: 4% 0%;
}

.inputdata {
    border: none !important;
    box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px, rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;
    margin: 6% 0%;
    background-image: url('../Images/plus.jpg');
    background-repeat: no-repeat;
    background-position: center right calc(.375em + .1875rem);
    background-size: calc(.75em + .375rem) calc(.75em + .375rem);

}

.about {
    background-color: #FFF0DD;
    padding: 4% 8%;
}

.para {
    color: #918e8b;
    padding-top: 2%;
    font-size: 19px;
}

.colorspan {
    color: #FD6C22;
}

.aboutdescription {
    padding: 4%;
    background-color: #FCFCFE;
}

.voteimg {
    width: 100%;
    height: 80vh;
}

.features {
    width: 100%;
    background-color: #FCFCFE;
    padding: 2%;
}

.featuretitle {
    color: #FD6C22;
}

.featurepara {
    text-align: left;
}


.featuremargin {
    margin-left: 7%;
}

.featureicon {
    background-color: #FFF0DD;
    height: 60px;
    width: 100px;
    border-radius: 50%;
    display: inline-block;
    margin-top: 2%;
}

.featureiconfa {
    color: #FD6C22;
    margin: auto;
    text-align: center;
    font-size: x-large;
    margin: 24%;
}